.articleWrapper {
  margin-bottom: 24px;

  &:last-of-type {
    margin-bottom: 0;
  }

  .articleWb {
    background: rgba(0, 0, 0, 0.2588235294);
    padding: 30px;
    border-radius: 20px;


    .header {
      // @media (min-width: 1000px) {
      //   margin-top: 40px;
      // }

      .articleHeaderWrapper {
        display: grid;
        grid-template-columns: minmax(0, 1fr) minmax(0, 2fr);
        grid-gap: 24px;

        @media (min-width: 1919px) {
          grid-template-columns: minmax(0, 1fr) minmax(0, 2fr);
        }

        @media (max-width: 999px) {
          grid-template-columns: minmax(0, 1fr);
          grid-template-rows: auto auto;
          grid-gap: calc(16px);
        }

        .articleImgHeader {

          display: -webkit-box;
          display: -webkit-flex;
          display: -ms-flexbox;
          display: flex;
          -webkit-flex-direction: column;
          -ms-flex-direction: column;
          flex-direction: column;

          @media (max-width: 1919px) {
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
          }

          .articleImgHeaderDescription {
            margin-top: 20px;
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            -webkit-flex-direction: column;
            -ms-flex-direction: column;
            flex-direction: column;

            .taskTitle {
              padding: 0;
              margin: 0;
              color: #E2E2E8;
              font-feature-settings: 'salt' on;
              font-size: 12px;
              font-weight: 400;
              letter-spacing: 1px;
              line-height: 120%;
              text-align: left;
              text-transform: uppercase;
              -webkit-text-size-adjust: 100%;
              -moz-text-size-adjust: 100%;
              -ms-text-size-adjust: 100%;
              text-size-adjust: 100%;
              display: -webkit-box;
              display: -webkit-flex;
              display: -ms-flexbox;
              display: flex;
              -webkit-align-items: center;
              -webkit-box-align: center;
              -ms-flex-align: center;
              align-items: center;
              -webkit-box-pack: center;
              -ms-flex-pack: center;
              -webkit-justify-content: center;
              justify-content: center;
              box-sizing: border-box;
              width: -webkit-fit-content;
              width: -moz-fit-content;
              width: fit-content;
              -webkit-column-gap: 4px;
              column-gap: 4px;
              margin: 0 0 8px 0;
              font-style: normal;
              font-weight: 400;
              font-size: 12px;
              line-height: 100%;
              color: #E2E2E8;
            }

            .taskSubTitle {
              padding: 0;
              margin: 0;
              color: #93949A;
              font-feature-settings: 'salt' on;
              font-size: 12px;
              font-weight: 400;
              letter-spacing: 1px;
              line-height: 120%;
              text-align: left;
              text-transform: uppercase;
              -webkit-text-size-adjust: 100%;
              -moz-text-size-adjust: 100%;
              -ms-text-size-adjust: 100%;
              text-size-adjust: 100%;
            }
          }
        }

        .articleDescriptionHeader {
          padding-bottom: 20px;
          border-bottom: 1px solid #e2e2e821;

          .articleDescriptionTitle {
            padding: 0;
            margin: 0;
            color: #E2E2E8;
            font-feature-settings: 'salt' on;
            font-size: 20px;
            font-weight: 500;
            line-height: 120%;
            text-align: left;
            text-transform: uppercase;
            -webkit-text-size-adjust: 100%;
            -moz-text-size-adjust: 100%;
            -ms-text-size-adjust: 100%;
            text-size-adjust: 100%;
            font-size: 32px;
            letter-spacing: inherit;

            @media (max-width: 999px) {
              font-size: 16px;
            }
          }

          .articleDescription {
            padding: 0;
            margin: 0;
            color: #93949A;
            font-feature-settings: 'salt' on;
            font-size: 24px;
            font-weight: 400;
            line-height: 120%;
            text-align: left;
            text-transform: none;
            -webkit-text-size-adjust: 100%;
            -moz-text-size-adjust: 100%;
            -ms-text-size-adjust: 100%;
            text-size-adjust: 100%;
            margin-top: 16px;
            letter-spacing: inherit;

            @media (max-width: 999px) {
              font-size: 14px;
            }
          }
        }
      }
    }

    .articleMain {
      margin-top: 37px;
      //margin-bottom: 37px;
      display: grid;
      grid-template-columns: calc((100% - 24px) / 3) calc((100% - 24px) * 2 / 3);
      grid-template-areas: '. content';
      grid-gap: 24px;

      @media (max-width: 672px) {
        margin-top: 37px;
      }

      @media (max-width: 999px) {
        grid-template-columns: 1fr;
        grid-template-rows: auto;
        grid-template-areas: 'content';
      }
    }

    .articleBlock {
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 140%;
      grid-area: content;
      background: #00000042;
      padding: 30px;
      border-radius: 20px;
      //border-bottom: 1px solid #E2E2E8;

      @media (max-width: 672px) {
        //padding-bottom: 37px;
        // margin-bottom: 37px;
      }

      @media (max-width: 999px) {
        font-size: 14px;
      }

      p {
        margin: 24px 0;
      }

      h2 {
        //margin-top: 64px;
      }
    }

    .articleText {
      padding: 0;
      margin: 0;
      color: #E2E2E8;
      font-family: "Arsenal", Roboto, system-ui, Tahoma, sans-serif;
      font-feature-settings: 'salt' on;
      font-size: 20px;
      font-weight: 400;
      line-height: 140%;
      text-align: left;
      text-transform: none;
      -webkit-text-size-adjust: 100%;
      -moz-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
      text-size-adjust: 100%;

      @media (max-width: 999px) {
        font-size: 14px;
      }
    }

    .articleTextTitle {
      padding: 0;
      margin: 0;
      color: #E2E2E8;
      font-family: "Arsenal", Roboto, system-ui, Tahoma, sans-serif;
      font-feature-settings: 'salt' on;
      font-size: 24px;
      font-weight: 900;
      line-height: 120%;
      text-align: left;
      text-transform: uppercase;
      -webkit-text-size-adjust: 100%;
      -moz-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
      text-size-adjust: 100%;

      @media (max-width: 999px) {
        font-size: 16px;
      }
    }

    .customCode {
      font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
      font-size: 16px;
      width: 80%;
    }
  }
}