.footer {
  width: 100%;
  border-top: 1px solid #fff;
  justify-content: center;
  display: flex;
  padding: 20px 0;
  //margin-top: 30px;

  @media (max-width: 999px) {
    margin-top: 0;
  }

  * {
    font-family: 'Oswald', Roboto, system-ui, Tahoma, sans-serif;
    color: #818181;
    font-size: .875rem;
    line-height: 1.25rem;
  }

  .footerLink {
    cursor: pointer;
    background: transparent;
    padding: 0;
    margin: 0;
    color: #E2E2E8;
    font-family: 'Oswald', Roboto, system-ui, Tahoma, sans-serif;
    font-feature-settings: 'salt' on;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: 1px;
    line-height: 120%;
    text-align: left;
    text-transform: uppercase;
    -webkit-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    text-size-adjust: 100%;
  }

  .point {
    height: 100%;
    display: flex;
    align-items: center;
  }

  .footerLinkWrapper {
    margin: 0 1rem;
    height: 100%;
    display: flex;
    align-items: center;
  }

  .footerWrapper {
    display: flex;
    max-width: 100%;

    height: 100%;
    display: -moz-flex;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    -moz-align-items: center;
    align-items: center;
    flex-direction: row;
  }

}